import { useUpdate } from "./useUpdate"
import { createId } from "../lib/createId"
import {useEffect, useState } from "react"

const useTags = () =>{
  const [tags,setTags] = useState<{id:number;name:string}[]>([])
  useEffect(()=>{
    let localTags = JSON.parse(window.localStorage.getItem('tags') || '[]')
    if(localTags.length<1){
      localTags = [
        {id:createId(),name:'衣'},
        {id:createId(),name:'食'},
        {id:createId(),name:'住'},
        {id:createId(),name:'行'},
      ]
    }
    setTags(localTags)
  },[])
  useUpdate(()=>{
    window.localStorage.setItem('tags',JSON.stringify(tags))
  },tags)
  const findTag = (id:number)=>tags.filter(t=>t.id===id)[0]
  const updateTag = (id:number, {name}:{name:string})=>{
    setTags(tags.map(tag=>tag.id===id?{id:id,name:name}:tag))
  }
  const deleteTag = (id:number)=>{
    setTags(tags.filter(tag=>tag.id!==id))
  }
  const addTag = ()=>{
    const tagName = window.prompt('请输入标签名称：')
    if(tagName!==null && tagName!==''){
      setTags([...tags,{id:createId(),name:tagName}])
    }
  }
  const getName = (id:number)=>{
   const tag = tags.filter(t=>t.id===id)[0]
    return tag.name
  }
  return {tags,getName,setTags,addTag,findTag,updateTag,deleteTag}
}

export {useTags}